<template>
  <div class="content">
    <van-dropdown-menu>
      <van-dropdown-item v-model="value1" :options="option1" />
      <van-dropdown-item v-model="value2" :options="option2" />
      <van-dropdown-item v-model="value3" :options="option3" />
    </van-dropdown-menu>
    <section class="mechanicItem">
      <div
        class="mechanic"
        v-for="(item,index) of this.getRespone.list"
        :key="index"
        :style="{'color':item.color,'background':item.bgColor}"
        @click="selectMechanic(item.mechaincState)"
      >
        <p>{{item.cardNum}}</p>
        <p>{{item.projectName}}</p>
        <div>
          {{item.state}}
          <p>{{item.sex == 1 ? '男':'女'}}</p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value1: 0,
      value2: "a",
      value3: 21,
      option1: [
        { text: "全部状态", value: 0 },
        { text: "空闲", value: 1 },
        { text: "上钟", value: 2 },
        { text: "加钟", value: 3 },
        { text: "暂离", value: 4 }
      ],
      option2: [
        { text: "全部工种", value: "a" },
        { text: "足疗", value: "b" },
        { text: "按摩", value: "c" },
        { text: "spa", value: "c" }
      ],
      option3: [
        { text: "全部技师", value: 21 },
        { text: "男", value: 22 },
        { text: "女", value: 23 }
      ]
    };
  },
  methods: {
    selectMechanic(index) {
      if (index !== 5) {
        this.$router.push({ path: "/mechanicOperation" });
      } else {
        this.$router.push({ path: "/appointmentDetail" });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.mechanicItem {
  @include grid(3, auto);
  padding: 0.5rem;
  .mechanic {
    @include wh(auto, 4.5rem);
    text-align: left;
    margin: 0.4rem;
    border-radius: 0.5rem;
    font-size: 0.8rem;
    padding: 0.5rem;
    p:first-child {
      font-weight: bold;
      margin-bottom: 0.1rem;
    }
    p:nth-child(2),
    div {
      font-size: 0.75rem;
    }
    div {
      @include fr(flex-start);
      p {
        margin-left: 0.6rem;
      }
    }
  }
}
</style>